import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router} from "@angular/router";

import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-modelpreview',
  templateUrl: 'modelpreview.template.html',
  styles: []
})
export class ModelpreviewComponent implements OnInit {

  constructor(private _http: HttpClient, private _router: Router, private _modalService: NgbModal) { }

  contract = {}
  Arr = Array;
  reviews = []
  
  ngOnInit() {
    this._http.get("assets/modelpreview.mock.json").subscribe(data => {this.contract = data});
  }

  buy(){
    //TODO buy
    this._router.navigate(['/mymodels']);
  }
  edit(){
    //TODO send edit
    this._router.navigate(['/editcontract', {id: this.contract["id"]}]);
  }
  
  seeReviews(content){
    this._http.get("assets/reviews.mock.json").subscribe(data => {
      this.reviews = data as any[]
      this._modalService.open(content).result.then((result) => {
        // this.closeResult = `Closed with: ${result}`;
        if(result == "no-model"){
          console.log("no-model");
          this._router.navigate(['/newcontract']);
        }else{
          this._router.navigate(['/fillcontract', {id: result}]);
        }
      }, (reason) => {
        // this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });
    });
  }
}



