import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router} from "@angular/router";

import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

import { ApiService } from '../api.service'

// let ECjs = require('./editcontract')

// declare var ECjs : any;

@Component({
  selector: 'app-editcontract',
  templateUrl: 'editcontract.template.html',
  styles: []
})
export class EditcontractComponent implements OnInit {

  constructor(private _apiService: ApiService, private _modalService: NgbModal, private _router: Router) { }

  options: Object = {
    heightMin: 400,
    heightMax: 400,
    pluginsEnabled: ['align', 'colors', 'lists']
  }

  politics = []
  autovars = []

  contract = {
    name: null,
    body: "Nuevo Documento",
    type: null
  }
  
  usedAutoVars = []

  newAutoVar = {
    name: "",
    type: null,
    valuesString: "",
    values: []
  };
  email = null;
  modelDescription = null;
  modelPrice = null;

  ngOnInit() {
    this._apiService.getContractToEdit(1).subscribe(data => {this.politics = data["politics"]; this.autovars = data["autovars"]; this.contract = data["contract"]});
  }

  addAutoVar(autovar){
    this.contract.body = this.contract.body + autovar.syntax;
    this.usedAutoVars.push(autovar)
  }

  openFile(event) {
    let input = event.target;
    for (var index = 0; index < input.files.length; index++) {
      let reader = new FileReader();
      reader.onload = () => {
        // this 'text' is the content of the file
        this.contract.body = reader.result;
      }
      reader.readAsText(input.files[index]);
    };
  }

  sendEmail(content){
    this._modalService.open(content).result.then((result) => {
      let data = {
        contract: this.contract,
        email: this.email
      }
      //TODO send email
      this._router.navigate(['/mycontracts']);
    }, (reason) => {
    });
  }

  open(content) {
    this._modalService.open(content, {size: 'lg'}).result.then((result) => {
      console.log(result)
      if (result == "final" || result == "draft"){
        this.contract.type = result;
        //TODO send contract
        this._router.navigate(['/mycontracts']);
      }else{
        this.saveAsModel(result);
      }
    }, (reason) => {
    });
  }

  newGuid(){
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
        return v.toString(16);
    });
  }

  saveAsModel(content){
    this._modalService.open(content).result.then((result) => {
      let mock = {
        contract: this.contract,
        type: result,
        price: this.modelPrice,
        description: this.modelDescription
      }
      //TODO send model

      let data = {
        "id": this.newGuid(),
        "name": this.contract.name,
        "description": this.modelDescription,
        "price": this.modelPrice,
        "ownerId": "68d5cb8c-1351-4776-bce2-9b360f4cd216",
        "stars": 0,
        "body": this.contract.body,
        "dynamicFields": [
          {
            "name": "string",
            "placeHolder": "string",
            "fieldType": "string",
            "values": [
              {}
            ]
          }
        ],
        "type": result
      }

      this._router.navigate(['/mymodels']);
    }, (reason) => {
    });
  }

  addVar(content) {
    this._modalService.open(content).result.then((result) => {
      this.newAutoVar.values = this.newAutoVar.valuesString.split(',')
      //TODO send var
      this.newAutoVar = {
        name: "",
        type: null,
        valuesString: "",
        values: []
      };
      this._apiService.getAutoVars().subscribe(data => {this.autovars = data as any[]});

    }, (reason) => {
    });
  }

  comparePolitics(){
    this._apiService.comparePolitics(this.contract.body).subscribe(data => {this.politics = data as any[]});
  }

  compareLastVersion(content){
    this._modalService.open(content, { size: 'lg', windowClass: 'compare-version-modal'}).result.then((result) => {
    }, (reason) => {
    });
  }

  delete(){
    //TODO send delete
    this._router.navigate(['/mycontracts']);
  }
}



