import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { FroalaEditorModule, FroalaViewModule } from 'angular-froala-wysiwyg';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { FormsModule } from '@angular/forms';

import { AppRoutingModule } from './app-routing/app-routing.module';
import { AppComponent, ReplaceUnderscore } from './app.component';
import { MarketplaceComponent } from './marketplace/marketplace.component';
import { LayoutComponent } from './layout/layout.component';
import { ModelpreviewComponent } from './modelpreview/modelpreview.component';
import { FillcontractComponent } from './fillcontract/fillcontract.component';
import { MycontractsComponent } from './mycontracts/mycontracts.component';
import { NewcontractComponent } from './newcontract/newcontract.component';
import { MymodelsComponent } from './mymodels/mymodels.component';
import { MypoliticsComponent } from './mypolitics/mypolitics.component';
import { EditcontractComponent } from './editcontract/editcontract.component';
import { PreviewcontractComponent } from './previewcontract/previewcontract.component';
import { LoginComponent } from './login/login.component';
import { ApiService } from './api.service';

@NgModule({
  declarations: [
    AppComponent,
    MarketplaceComponent,
    LayoutComponent,
    ModelpreviewComponent,
    FillcontractComponent,
    MycontractsComponent,
    ReplaceUnderscore,
    NewcontractComponent,
    MymodelsComponent,
    MypoliticsComponent,
    EditcontractComponent,
    PreviewcontractComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    FormsModule,
    NgbModule.forRoot(),
    FroalaEditorModule.forRoot(), 
    FroalaViewModule.forRoot()
  ],
  providers: [ApiService],
  bootstrap: [AppComponent]
})
export class AppModule { }



