import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router} from "@angular/router";
import { HttpHeaders } from '@angular/common/http';

declare var jquery:any;
declare var $ :any;


const httpOptions = {
  crossDomain: true,
  headers: new HttpHeaders({
    'Content-Type':  'application/jsonp',
    'cache-control': 'no-cache'
  }),
  dataType: 'jsonp'
};

import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-newcontract',
  templateUrl: 'newcontract.template.html',
  styles: []
})
export class NewcontractComponent implements OnInit {

  constructor(private _http: HttpClient, private _modalService: NgbModal, private _router: Router) { }

  options: Object = {
    heightMin: 400,
    heightMax: 400,
    pluginsEnabled: ['align', 'colors', 'lists']
  }

  politics = []
  autovars = [
    {
      "name": "[[plazo]]",
      "placeHolder": "Plazo",
      "fieldType": "int",
    },{
      "name": "[[monto]]",
      "placeHolder": "Monto",
      "fieldType": "float",
    }
  ]

  contract = {
    name: null,
    body: "Nuevo Documento",
    type: null
  }
  
  usedAutoVars = []

  newAutoVar = {
    name: "",
    fieldType: "",
    valuesString: "",
    placeHolder: "",
    values: []
  };
  email = null;
  modelDescription = null;
  modelPrice = null;

  ngOnInit() {
    this._http.get("assets/newcontract.mock.json").subscribe(data => {this.politics = data["politics"];});
  }

  addAutoVar(autovar){
    this.contract.body = this.contract.body + autovar.syntax;
  }

  openFile(event) {
    let input = event.target;
    for (var index = 0; index < input.files.length; index++) {
      let reader = new FileReader();
      reader.onload = () => {
        // this 'text' is the content of the file
        this.contract.body = reader.result;
      }
      reader.readAsText(input.files[index]);
    };
  }

  sendEmail(content){
    this._modalService.open(content).result.then((result) => {
      let data = {
        contract: this.contract,
        email: this.email
      }
      //TODO send email
      this._router.navigate(['/mycontracts']);
    }, (reason) => {
    });
  }

  open(content) {
    this._modalService.open(content, {size: 'lg'}).result.then((result) => {
      console.log(result)
      if (result == "final" || result == "draft"){
        this.contract.type = result;
        //TODO send contract
        this._router.navigate(['/mycontracts']);
      }else{
        this.saveAsModel(result);
      }
    }, (reason) => {
    });
  }

  newGuid(){
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
        return v.toString(16);
    });
  }

  saveAsModel(content){
    this._modalService.open(content).result.then((result) => {
      let mock = {
        contract: this.contract,
        type: result,
        price: this.modelPrice,
        description: this.modelDescription
      }
      // let data = {
      //   id: "",
      //   name: this.contract.name,
      //   description: this.modelDescription,
      //   price: this.modelPrice,
      //   ownerId: "68d5cb8c-1351-4776-bce2-9b360f4cd216",
      //   stars: 0,
      //   body: this.contract.body,
      //   dynamicFields: [],
      //   type: result
      // }
      // let data = {
      //   "id": "",
      //   "name": "GIANLUCA",
      //   "description": "Este es un contrato.",
      //   "price": 100,
      //   "ownerId": "68d5cb8c-1351-4776-bce2-9b360f4cd216",
      //   "stars": 0,
      //   "body": "loep dfsdfds loep dfsdfdsloep dfsdfdsloep dfsdfdsloep dfsdfds loep dfsdfdsloep dfsdfdsloep dfsdfds",
      //   "dynamicFields": [],
      //   "type": "public"
      // }
      // console.log(data)
      // debugger
      // this._http.post("http://52.203.241.165:8000/marketplace/", data, httpOptions).subscribe(data => {this._router.navigate(['/mymodels']);});

      var settings = {
        "async": true,
        "crossDomain": true,
        "url": "http://52.203.241.165:8000/marketplace/",
        "method": "POST",
        "headers": {
          "content-type": "application/json",
          "cache-control": "no-cache"
        },
        "processData": false,
        "data": "{\n  \"id\": \"\",\n  \"name\": \"GIANLUCA\",\n  \"description\": \"Este es un contrato.\",\n  \"price\": 100,\n  \"ownerId\": \"68d5cb8c-1351-4776-bce2-9b360f4cd216\",\n  \"stars\": 0,\n  \"body\": \"loep dfsdfds loep dfsdfdsloep dfsdfdsloep dfsdfdsloep dfsdfds loep dfsdfdsloep dfsdfdsloep dfsdfds\",\n  \"dynamicFields\": [],\n  \"type\": \"public\"\n}"
      }
      
      $.ajax(settings).done(function (response) {
        console.log(response);
      });
      //TODO send model
    }, (reason) => {
    });
  }

  addVar(content) {
    this._modalService.open(content).result.then((result) => {
      this.newAutoVar.values = this.newAutoVar.valuesString.split(',')
      this.autovars.push(this.newAutoVar);
      this.newAutoVar = {
        name: "",
        fieldType: "",
        placeHolder: "",
        valuesString: "",
        values: []
      };
    }, (reason) => {
    });
  }

  comparePolitics(){
    this._http.get("assets/comparepolitics.mock.json").subscribe(data => {this.politics = data as any[]});
  }

}



