import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router} from "@angular/router";

@Component({
  selector: 'app-marketplace',
  templateUrl: 'marketplace.template.html',
  styles: []
})
export class MarketplaceComponent implements OnInit {

  constructor(private _http: HttpClient, private _router: Router) { }

  models = []
  Arr = Array;
  
  ngOnInit() {
    this._http.get("http://52.203.241.165:8000/marketplace/").subscribe(data => {console.log(data); this.models = data as any[];});
  }

  buy(model){
    //TODO buy
    this._router.navigate(['/mymodels']);
  }

  preview(model){
    //TODO buy
    this._router.navigate(['/modelpreview', { id: model.id }]);
  }

}



