import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router} from "@angular/router"

import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { ApiService } from '../api.service'

@Component({
  selector: 'app-mypolitics',
  templateUrl: 'mypolitics.template.html',
  styles: []
})
export class MypoliticsComponent implements OnInit {

  constructor(private _http: HttpClient, private _modalService: NgbModal, private _apiService: ApiService) { }

  politicData = {
    // id: null,
    clause: null,
    description : null,
    type : null
  }

  politics = []

  ngOnInit() {
    this._http.get("http://52.203.241.165:8000/organization/" + this._apiService.organizationId).subscribe(data => {this.politics = data["clauses"] as any[]});
  }

  editPolitic(content, politic) {
    this.politicData = {
      // id: politic.id,
      description: politic.description,
      clause: politic.clause,
      type : politic.type
    }
    this._modalService.open(content).result.then((result) => {
      if(result == "remove"){
        //TODO remove
      }else if(result == "save"){
        //TODO save changes
      }
      this._http.get("assets/mypolitics.mock.json").subscribe(data => {this.politics = data as any[]});
    }, (reason) => {
    });
  }

  newPolitic(content) {
    this.politicData = {
      clause: null,
      description : null,
      type : null
    }
    this._modalService.open(content).result.then((result) => {
      //TODO send politic
      this._http.get("assets/mypolitics.mock.json").subscribe(data => {this.politics = data as any[]});
    }, (reason) => {
    });
  }

}



