import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MarketplaceComponent } from '../marketplace/marketplace.component'
import { ModelpreviewComponent } from '../modelpreview/modelpreview.component'
import { LayoutComponent } from '../layout/layout.component'
import { FillcontractComponent } from '../fillcontract/fillcontract.component'
import { MycontractsComponent } from '../mycontracts/mycontracts.component'
import { NewcontractComponent } from '../newcontract/newcontract.component'
import { MymodelsComponent } from '../mymodels/mymodels.component'
import { MypoliticsComponent } from '../mypolitics/mypolitics.component'
import { EditcontractComponent } from '../editcontract/editcontract.component'
import { PreviewcontractComponent } from '../previewcontract/previewcontract.component'
import { LoginComponent } from '../login/login.component';

const routes: Routes = [
  { path: '', component: LoginComponent, pathMatch: 'full' },
  { path: '', 
    component: LayoutComponent, 
    children: [
      {
        path: 'marketplace',
        component: MarketplaceComponent,
      },{
        path: 'mymodels',
        component: MymodelsComponent,
      },{
        path: 'modelpreview',
        component: ModelpreviewComponent,
      },{
        path: 'fillcontract',
        component: FillcontractComponent,
      },{
        path: 'mycontracts',
        component: MycontractsComponent,
      },{
        path: 'newcontract',
        component: NewcontractComponent
      },{
        path: 'mypolitics',
        component: MypoliticsComponent,
      },{
        path: 'editcontract',
        component: EditcontractComponent,
      },{
        path: 'previewcontract',
        component: PreviewcontractComponent,
      }
    ]
  },
  // { path: '**', redirectTo: '/products'}
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ],
  declarations: []
})
export class AppRoutingModule { }


