import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from "@angular/router"

import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import {ApiService} from "../api.service"

@Component({
  selector: 'app-mycontracts',
  templateUrl: 'mycontracts.template.html',
  styles: []
})
export class MycontractsComponent implements OnInit {

  constructor(private _http: HttpClient, private _modalService: NgbModal, private _router: Router, private _apiService: ApiService) { }

  contracts = []
  models = []

  ngOnInit() {
    this._http.get("http://52.203.241.165:8000/organization/" + this._apiService.organizationId + "/contract").subscribe(data => { this.contracts = data as any[] });
  }

  newContract(content) {
    this._http.get("http://52.203.241.165:8000/organization/" + this._apiService.organizationId + "/marketplace").subscribe(data => { this.models = data as any[] });
    this._modalService.open(content, { size: 'lg' }).result.then((result) => {
      // this.closeResult = `Closed with: ${result}`;
      if (result == "no-model") {
        console.log("no-model");
        this._router.navigate(['/newcontract']);
      } else {
        this._router.navigate(['/fillcontract', { id: result }]);
      }
    }, (reason) => {
      // this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  randomDate(start, end) {
    return new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()));
  }

  dateDiff(date) {
    // let date1 = new Date(date);
    let today = new Date();
    let date1 = this.randomDate(new Date(2018, 0, 1),new Date(2024, 0, 1)) 
    return this.calcDate(date1,today)
    // return date1;
  }

  calcDate(date1, date2) {
    if(date2 > date1){
      return "Vencido"
    }
    var diff = Math.floor(date1.getTime() - date2.getTime());
    var day = 1000 * 60 * 60 * 24;
    // console.log(date1);
    // console.log(date2)
    var days = Math.floor(diff / day);
    var months = Math.floor(days / 31);
    var years = Math.floor(months / 12);
    months = months - years*12;
    days = days - months*31 - years*12*31;

    var message = "";
    message += ""
    if(years)
      message += years + " años "
    if(months)
    message += months + " meses "
    if(days)
      if(days == 1)
        message += days + " día "
      else
      message += days + " días "
    

    return message
  }

  selectContract(contract) {
    if (contract.type == "final") {
      this._router.navigate(['/previewcontract', { id: contract.id }]);
    } else {
      this._router.navigate(['/editcontract', { id: contract.id }]);
    }
  }

}



