import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router, Route} from "@angular/router";

@Component({
  selector: 'app-fillcontract',
  templateUrl: 'fillcontract.template.html',
  styles: []
})
export class FillcontractComponent implements OnInit {

  constructor(private _http: HttpClient, private _router: Router) { }

  data = {}

  ngOnInit() {
    this._http.get("assets/fillcontract.mock.json").subscribe(data => {this.data = data});
  }

  send(){
    //TODO send fill
    this._router.navigate(['/editcontract']);
  }

}



