import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router} from "@angular/router"

import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import {ApiService} from '../api.service'

@Component({
  selector: 'app-mymodels',
  templateUrl: 'mymodels.template.html',
  styles: []
})
export class MymodelsComponent implements OnInit {

  constructor(private _http: HttpClient, private _modalService: NgbModal, private _router: Router, private _apiService:ApiService) { }

  models = []
  Arr = Array;
  
  ngOnInit() {
    this._http.get("http://52.203.241.165:8000/organization/" + this._apiService.organizationId + "/marketplace").subscribe(data => {this.models = data as any[];});
  }

  newContract(content) {
    this._modalService.open(content, { size: 'lg' }).result.then((result) => {
      // this.closeResult = `Closed with: ${result}`;
      if(result == "no-model"){
        console.log("no-model");
        this._router.navigate(['/newcontract']);
      }
    }, (reason) => {
      // this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  preview(){
    this._router.navigate(['/modelpreview']);
  }

}



