import { Injectable } from '@angular/core';
import { Response, Headers, RequestOptions } from '@angular/http';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ApiService {

  constructor(private _http: HttpClient) { }

  organizationId = "570d824f-f2b2-454e-bdea-3663cbc59597"

  getContractToEdit(id): Observable<any> {
    return this._http.get("assets/editcontract.mock.json");
  }

  getAutoVars(): Observable<any> {
    return this._http.get("assets/autovars.mock.json");
  }

  comparePolitics(contractBody): Observable<any> {
    return this._http.get("assets/comparepolitics.mock.json");
  }

}



