import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Router} from "@angular/router";

@Component({
  selector: 'app-previewcontract',
  templateUrl: 'previewcontract.template.html',
  styles: []
})
export class PreviewcontractComponent implements OnInit {

  constructor(private _http: HttpClient, private _router: Router) { }

  contract = {}
  Arr = Array;

  ngOnInit() {
    this._http.get("assets/modelpreview.mock.json").subscribe(data => {this.contract = data});
  }

}



